p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
ul,
li,
a,
button {
  all: unset;
  cursor: pointer;
}

/*=====NAVBAR=====*/
/*=====NAVBAR=====*/
#nav {
  position: sticky;
  top: 0;
  height: 10vh;
  z-index: 99999;
}
.nav {
  padding: 10px 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary-bg-color);
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
}
.nav .carrer {
  font-size: 35px;
  font-weight: 700;
  color: var(--primary-color);
}
.nav .path {
  font-weight: 700;
  font-size: 35px;
  /* font-family: 'Trebuchet MS', sans-serif; */
  color: var(--secondary-color);
}
.nav .navLeft {
  display: flex;
  gap: 150px;
  align-items: center;
}
.navLeft .SearchBar {
  display: flex;
  align-items: center;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.26);
  padding: 5px 20px;
}
.navLeft .SearchBar .search-icon {
  font-size: 25px;
  color: rgba(0, 0, 0, 0.16);
}
.navLeft .SearchBar input[type="text"] {
  /* outline: none; */
  border: none;
  outline: none;
  width: 100%;
  padding: 5px 30px 5px 10px;
  font-size: 15px;
}
.nav .NavMenu {
  display: flex;
  align-items: center;
  gap: 30px;
}
.NavMenu .setting,
.NavMenu .mode,
.NavMenu .notification {
  font-size: 25px;
  cursor: pointer;
}
.NavMenu .notification {
  color: var(--primary-color);
}
.nav .NavMenu img {
  width: 50px;
  height: 50px;
}
@media screen and (max-width: 769px) {
  .nav {
    padding: 10px;
    height: 6vh;
  }
  .nav .carrer {
    font-size: 20px;
  }
  .nav .path {
    font-size: 20px;
  }
  .nav .navLeft {
    gap: 10px;
  }
  .navLeft .SearchBar {
    width: 100px;
    padding: 2px 10px;
  }
  .navLeft .SearchBar .search-icon {
    font-size: 20px;
  }
  .navLeft .SearchBar input[type="text"] {
    padding: 5px;
    font-size: 10px;
  }
  .nav .NavMenu {
    gap: 7px;
  }
  .NavMenu .setting,
  .NavMenu .mode,
  .NavMenu .notification {
    font-size: 15px;
  }
  .nav .NavMenu img {
    width: 25px;
    height: 25px;
  }
}

/*=====LEFT NAVBAR=====*/
/*=====LEFT NAVBAR=====*/
#LeftNavBar {
  width: 200px;
  /* height: 80vh; */
  padding: 20px 30px;
  background-color: var(--primary-bg-color);
  position: fixed;
  left: 0;
  top: 80px;
  display: flex;
  flex-direction: column;
  /* align-content: space-between; */
  /* justify-content: space-between; */
  gap: 50px;
  /* justify-content: center; */
}

.active{
  color: var(--primary-color);
  font-weight: 600;
}
#LeftNavBar .Upper ul {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

#LeftNavBar .Upper ul li {
  display: flex;
  align-items: center;
  gap: 10px;
  border-radius: 8px;
  width: 150px;
  /* width: max-content; */
  padding: 10px 15px;
  box-shadow: rgba(0, 0, 0, 0.07) 0px 1px 1px, rgba(0, 0, 0, 0.07) 0px 2px 2px,
    rgba(0, 0, 0, 0.07) 0px 4px 4px, rgba(0, 0, 0, 0.07) 0px 8px 8px,
    rgba(0, 0, 0, 0.07) 0px 16px 16px;
  /* color:var(--tertiary-text-color) */
}
.leftNavbar-icon {
  font-size: 20px;
}
.Lower .StudentSupport h3 {
  margin-bottom: 10px;
}
.Lower .StudentSupport p {
  font-size: var(--phone-paragraph-font);
  margin-bottom: 15px;
}
.StudentSupport .support-icon {
  font-size: 30px;
}
.Lower .StudentSupport button {
  border-radius: 8px;
  display: flex;
  gap: 5px;
  align-items: center;
  background-color: #6271ff42;
  color: #6271ff;
  font-weight: 700;
  padding: 5px 10px;
  font-size: var(--teritary-phone--font);
}
@media screen and (max-width: 769px) {
  #LeftNavBar {
    width: 50px;
    padding: 50px 10px;
    height: max-content;
    top: 15vh;
    border-radius: 5px;
  }
  #LeftNavBar .Upper ul li {
    width: max-content;
  }
  .leftNavbar-icon {
    font-size: 15px;
  }
  #LeftNavBar .Upper ul li span {
    display: none;
  }
  .Lower .StudentSupport h3,
  .Lower .StudentSupport p {
    display: none;
  }

  .Lower .StudentSupport button {
    flex-direction: column;
    gap: 5px;

    padding: 5px 10px;
    font-size: 8px;
  }
}

