/*=====CARRER PATH PAGE=====*/
/*=====CARRER PATH PAGE=====*/
#Container {
  margin: 50px 38px 50px 300px;
}
@media screen and (max-width: 769px) {
  #Container {
    margin: 30px 15px 30px 85px;
  }
}

.CarrerPath .Heading h2,
.Courses .Heading h2 {
  font-size: var(--secondary-desktop--font);
  margin: 10px 0;
}
.CarrerPath .CardContainer,
.CourseCardContainer,
.FreeCourseContainer {
  margin: 30px;
  display: flex;
  flex-wrap: wrap;

  justify-content: space-between;
  gap: 50px;
}
.CarrerPath .Card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  gap: 10px;
  border-radius: 7px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.16);
  width: calc(100% / 3.4);
  transition: all 0.5s ease-in-out;
  background-color: var(--primary-bg-color);
}
.CarrerPath .Card:hover {
  transform: scale(1.05);
}
.CarrerPath .Card .Desc {
  padding: 0 10px;
}
.CarrerPath .Card .Salary {
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  gap: 93px;
  color: var(--tertiary-text-color);
}
.CarrerPath .Card img {
  width: 100%;
  object-fit: cover;
  border-radius: 7px 7px 0 0;
}
.CarrerPath .Card button,
.CourseCardContainer .CourseCard button,
.FreeCourseCard button {
  padding: 8px 25px;
  margin: 15px 0;
  border-radius: 4px;
  background-color: #0038a3cc;
  color: var(--primary-bg-color);
  transition: all 0.5s;
}
.CarrerPath .Card button:hover,
.FreeCourseCard button:hover {
  background-color: var(--primary-color);
}
@media screen and (max-width: 769px) {
  .CarrerPath .CardContainer {
    margin: 10px;
    flex-direction: column;
  }
  .CarrerPath .Heading h2,
  .Courses .Heading h2 {
    font-size: var(--primary-phone--font);
    margin: 30px 0;
  }
  .CarrerPath .Card,
  .CourseCardContainer .CourseCard {
    width: fit-content;
  }
}

/*=====COURSES PAGE=====*/
/*=====COURSES PAGE=====*/
.CourseCardContainer {
  margin: 20px 100px;
}
.CourseCard .freeimg {
  display: flex;
  justify-content: space-between;
}
.CourseCard > img {
  display: inline-block;
}
.CourseCardContainer .CourseCard {
  width: calc(100% / 3);
  padding: 0 15px 20px 15px;
  background-color: #fafafa;
  border-radius: 7px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  transition: all 0.5s ease-in-out;
}
.CourseCard:hover {
  transform: scale(1.05);
}
.CourseCardContainer .CourseCard ul {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 20px 10px 10px 10px;
}
.CourseCardContainer .CourseCard h3 {
  font-size: var(--tertiary-desktop--font);
  margin: 10px 0 20px 0;
  display: inline-block;
}
.CourseCardContainer .CourseCard h4 {
  font-size: 20px;
}
.CourseCardContainer .CourseCard ul li {
  display: flex;
  align-items: center;
  gap: 10px;
}
.CourseCardContainer .CourseCard button {
  display: flex;
  margin: 20px auto;
}
.star-icon {
  color: var(--tertiary-color);
}
.CourseCardContainer .Icons {
  display: flex;
  gap: 10px;
  align-items: center;
  margin-bottom: 20px;
}
.CourseCardContainer .Icons img {
  width: 20px;
  height: 20px;
}
@media screen and (max-width: 769px) {
  .CourseCardContainer {
    margin: 10px;
    flex-direction: column;
  }

  .Courses .Heading h2 {
    font-size: var(--primary-phone--font);
    margin: 30px 0;
  }

  .CourseCardContainer .CourseCard {
    width: fit-content;
  }
  .CourseCardContainer .CourseCard h3 {
    font-size: var(--secondary-phone--font);
    margin: 10px 0 20px 0;
    display: inline-block;
  }
  .CourseCardContainer .CourseCard h4 {
    font-size: 18px;
  }
}

/*======FREE COURSE======*/
/*======FREE COURSE======*/
.FreeCourseContainer {
  gap: 10px;
  row-gap: 40px;
}
.FreeCourseContainer .FreeCourseCard {
  width: calc(100% / 3.5);
  border-radius: 7px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  border-radius: 7.859px;
  background: #fff;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
  /* gap:10px; */
  background-color: var(--primary-bg-color);
  transition: all 0.5s ease-in-out;
}
.FreeCourseCard:hover {
  transform: scale(1.05);
}
.FreeCourseCard .FreeCourseCardTop {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.FreeCourseCard img {
  width: 18px;
}
.FreeCourseCard .certificate {
  font-size: var(--phone-paragraph-font);

  font-weight: 500;
}
.FreeCourseCard .certificate span {
  display: flex;
  gap: 7px;
  align-items: center;
}

.FreeCourseCard .desc {
  color: var(--tertiary-text-color);
  margin-bottom: 10px;
}
.FreeCourseCard .FreeCourseCardRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 500;
  margin-bottom: 7px;
  font-size: var(--phone-paragraph-font);
}
.FreeCourseCard .FreeCourseCardRow span {
  display: flex;
  gap: 7px;
  align-items: center;
}
.FreeCourseCard button {
  width: max-content;
  align-self: flex-end;
  margin: 0;
}
@media screen and (max-width: 769px) {
  .FreeCourseContainer .FreeCourseCard {
    width: 100%;
  }
  .FreeCourseCard h3 {
    font-size: var(--tertiary-phone--font);
  }
  .FreeCourseCard img {
    width: 13px;
  }
  .FreeCourseCard .certificate {
  }
}
/*======ROAD MAP======*/
/*======ROAD MAP======*/
.RoadMapContainer {
  display: flex;
  gap: 50px;
  margin: 30px;
}
.RoadMapContainer .Left {
  display: flex;
  flex-direction: column;
  padding: 15px;
  align-items: center;

  flex: 0.3;
  border-radius: 10px;
  background: #fff;
  box-shadow: 3px 2px 0px 0px rgba(0, 0, 0, 0.23);
}
.RoadMapContainer .Left .box {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 15px;
  align-items: center;
  border: 1px solid var(--secondary-text-color);
  border-radius: 10px;
}
.RoadMapContainer .Left .box h3 {
  font-size: var(--desktop-paragraph-font);
}
.RoadMapContainer .Left .box p {
  text-align: center;
}
.RoadMapContainer .Left .box tr{
  display: flex;
  gap:10px;

}
.RoadMapContainer .Left .box button {
  color: var(--primary-color);
  padding: 5px 15px;
  font-weight: 500;
  border-radius: 10px;

  box-shadow: 2px 1px 8px 0px rgba(0, 0, 0, 0.12);
}
.RoadMapContainer .arrow {
  height: 30px;
  margin: 0;
  color: var(--secondary-text-color);
}
.RoadMapContainer .Left a,
.Dashboard .LeftBox2 a,
.Dashboard .LeftBox1 a {
  display: flex;
  margin-top: 5px;
  align-self: flex-end;
  /* align-items: flex-end; */
  font-weight: 700;
  color: var(--primary-color);
}

.RoadMapContainer .Right {
  display: flex;
  flex: 0.7;
  flex-direction: column;
  padding: 15px;
  gap: 30px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 3px 2px 0px 0px rgba(0, 0, 0, 0.23);

  background-color: var(--primary-bg-color);
}
/* .RoadMapContainer .RightTop{
  display:flex;
  flex-direction: column;
  align-content: space-between;
  justify-content: space-between;
} */
.RoadMapContainer .Right .RightTopRow-1,
.RoadMapContainer .Right .RightTopRow-2 {
  display: flex;
  border-radius: 10px;
  padding: 15px;
  gap: 25px;
}
.RoadMapContainer .Right .RightTopRow-1 div,
.RoadMapContainer .Right .RightTopRow-2 div {
  border: 1px solid var(--secondary-text-color);
  border-radius: 10px;
  padding: 15px;
  width: calc(100% / 2);
}

.RoadMapContainer .Right h4 {
  font-size: var(--desktop-paragraph-font);
}
 .RoadMapContainer .Right ul {
  display: flex;
  flex-direction: column;
  gap: 7px;
  padding: 10px;
}
.RoadMapContainer .Right ul li {
  display: flex;
  align-items: center;
  gap: 10px;
}
.tick {
  font-size: 25px;
  color: var(--primary-color);
}
.cross {
  margin-left: 5px;
  font-size: 15px;
  color: var(--secondary-color);
}
.RoadMapContainer .RightBottom {
  padding: 15px;
}
.RoadMapContainer .RightBottom h3 {
  font-size: var(--tertiary-desktop--font);
  margin: 10px 0;
  color: var(--primary-color);
}
@media screen and (max-width: 769px) {
  .RoadMapContainer {
    flex-direction: column;
    gap: 20px;
    margin: 20px 0;
  }
  .RoadMapContainer .Right {
    padding: 0;
  }
  .RoadMapContainer .Right .RightTopRow-1,
  .RoadMapContainer .Right .RightTopRow-2 {
    display: flex;
    flex-direction: column;
    gap: 25px;
    padding: 10px;
  }
  .RoadMapContainer .Right .RightTopRow-1 div,
  .RoadMapContainer .Right .RightTopRow-2 div {
    width: calc(90%);
    padding: 10px;
  }
  .RoadMapContainer .RightBottom h3 {
    font-size: var(--tertiary-phone--font);
  }
  .RoadMapContainer .Review img {
    width: 30px;
    height: 30px;
  }
}
